/* eslint-disable new-cap */
import { LitElement, html, } from 'lit-element';
import { getComponentSharedStyles, } from '@cells-components/cells-lit-helpers';
import { ifDefined } from 'lit-html/directives/if-defined.js';
import { CellsFocusVisibleMixin } from '@cells-components/cells-focus-visible-mixin';
import { add as GesturesAddListener } from '@cells-components/cells-lit-helpers/utils/gestures.js';
import styles from './cells-radio-styles.js';
/**
`CellsRadio` provides a base class as well as a `<cells-radio>` custom element with input radio functionality as close as possible to native radios. It provides an easier and convenient way to build radios with encapsulated HTML & custom styles. As it almost does not provide any styles, it's specially aimed to be extended.

As native radios, it can participate in forms in the same document using standard attributes. For example, using 'required' on a radio will prevent a parent form from being submitted if no radio from that group has been selected.

Radios can be grouped using just the same 'name' attribute on them, as if they were native radio inputs. However, it's recommended to add a `role='radiogroup'` to the radios container for accessibility reasons.

`CellsRadio` is heavily based on [weightless.dev radio](https://weightless.dev/elements/radio) for the form communication, and on [Vaadin Radio Button](https://vaadin.com/components/vaadin-radio-button) for the active state management.

When extending from `CellsRadio`, make sure to:
- Call `CellsRadio` styles using `super.shadyStyles` if you want to use its basic styling
- Use `this._radio` getter in render to enable communication with native forms.

You can overwrite the radio dot HTML using the `inputTemplate` template getter. Also, you can overwrite the label HTML using the `labelTemplate` template getter.

**Example**
```js
// Extending CellsRadio to create a CustomRadio element
class CustomRadio extends CellsRadio {
  static get properties() {
    return {
      // Additional properties here
    };
  }

  static get shadyStyles() { // get styles from upper class using super
    return `
      ${super.shadyStyles}
      ${styles.cssText}
      ${getComponentSharedStyles('custom-radio-shared-styles').cssText}
    `;
  }

  render() { // include any HTML you need; for example, icons
    return html`
      <style>${this.constructor.shadyStyles}</style>
      ...
      ${this._radio}
    `;
  }
}

customElements.define('custom-radio', CustomRadio);
```

```html
<div role="radiogroup">
  <custom-radio name="custom" value="radio1">Radio 1</custom-radio>
  <custom-radio name="custom" value="radio2" disabled>Radio 2</custom-radio>
  <custom-radio name="custom" value="radio3">Radio 3</custom-radio>
  <custom-radio name="custom" value="radio4">Radio 4</custom-radio>
</div>
```

## FocusVisible mixin

`CellsRadio` uses `FocusVisible mixin` from `@cells-components`. This means you can use the 'focus-visible' attribute on your component to manage ':focus' styles based on user navigation interface (keyboard/pointer).

```css
:host([focus-visible]) {
  // Your focus styles
}
```

## Caveats

### 'active' state

You can use the `:active` CSS pseudo-class to define styles for the radio when it's being activated by the user using the mouse. However, this won't work when button is activated using the keyboard. `CellsRadio` provides an `active` attribute which gets triggered on keydown and keyup with Spacebar/Enter, as well as with down and up mouse actions (touch or pointer).

```css
:host([active]) {
  ... Active styles ...
}
```

* @customElement
* @demo demo/index.html
* @extends {LitElement}
* @appliesMixin { CellsFocusVisibleMixin }
*/
export class CellsRadio extends CellsFocusVisibleMixin(LitElement) {
  static get is() {
    return 'cells-radio';
  }

  static get properties() {
    return {
      /**
       * Disabled state of radio
       */
      disabled: {
        type: Boolean,
        reflect: true
      },

      readonly: {
        type: Boolean
      },
      /**
       * Radio value related to forms
       */
      value: {
        type: String,
        reflect: true
      },
      /**
       * Checked status of radio
       */
      checked: {
        type: Boolean,
        reflect: true
      },
      /**
       * If true, selecting one radio from this radio group (radios with same name) will be required
       */
      required: {
        type: Boolean
      },
      /**
       * Name of radio related to forms
       */
      name: {
        type: String,
        reflect: true
      },
      /**
       * Associated form for radio
       */
      form: {
        type: String
      },

      _formElementId: {
        type: String
      }
    };
  }

  constructor() {
    super();
    this.checked = false;
    this.disabled = false;
    this.readonly = false;
    this._active = false;
    this._formElementId = this.uniqueID();
  }

  /**
   * Returns unique ID string
   * @param  {Number} length Length of ID
   * @return {String}        String of random number
   */
  uniqueID(length = 10) {
    return `_${Math.random().toString(36).substr(2, length)}`;
  }

  get _disabled() {
    return this.disabled || (this.readonly && !this.checked);
  }

  connectedCallback() {
    super.connectedCallback();
    this._setInitialAttribute('role', 'radio');
    this.onClick = this.onClick.bind(this);
    this.addEventListener('click', this.onClick);
    this.onKeyUp = this.onKeyUp.bind(this);
    this.onKeyDown = this.onKeyDown.bind(this);
    this.addEventListener('keyup', this.onKeyUp);
    this.addEventListener('keydown', this.onKeyDown);
    GesturesAddListener(this, 'down', () => !this._disabled && (this.active = true));
    GesturesAddListener(this, 'up', () => this.active = false);
    this.addEventListener('blur', () => this.active = false);
    if (this.hasAttribute('autofocus')) {
      this._updateTabIndex();
      this.focus();
    }
  }

  disconnectedCallback() {
    super.disconnectedCallback();
    this.removeEventListener('click', this.onClick);
    this.removeEventListener('keydown', this.onKeyDown);
    if (this.active) {
      this.active = false;
    }
  }

  firstUpdated(changedProps) {
    super.firstUpdated(changedProps);
    this._formElement = this.shadowRoot.querySelector(`#${this._formElementId}`);
    if (this._formElement) {
      this.appendChild(this._formElement);
    }
  }

  updated(changedProps) {
    if (changedProps.has('disabled') || changedProps.has('readonly')) {
      this.setAttribute('aria-disabled', this._disabled.toString());
    }
    if (changedProps.has('checked')) {
      this.setAttribute('aria-checked', this.checked.toString());
      if (this.checked) {
        this._uncheckGroup();
        this._dispatchEvent('change');
        this._dispatchEvent('input');
      }
    }
    if (changedProps.has('disabled') || changedProps.has('readonly') || changedProps.has('checked')) {
      this._updateTabIndex();
    }
  }

  /**
   * Active state of radio
   * @return {Boolean} Active state of radio
   */
  get active() {
    return this._active;
  }

  /**
   * Sets active state for radio
   * @param  {Boolean} value Active state for radio
   */
  set active(value) {
    if (value !== this._active) {
      this._active = value;
      if (value) {
        this.setAttribute('active', '');
      } else {
        this.removeAttribute('active');
      }
    }
  }

  static get shadyStyles() {
    return `
      ${styles.cssText}
      ${getComponentSharedStyles('cells-radio-shared-styles').cssText}
    `;
  }

  render() {
    return html`
      <style>${this.constructor.shadyStyles}</style>
      ${this.labelledInput}
      ${this._radio}
    `;
  }

  /**
   * HTML structure combining dot (inputTemplate) and label (labelTemplate)
   * @return {TemplateResult} HTML of visible dot and label
   */
  get labelledInput() {
    return html`
      <div class="container">
        ${this.inputTemplate}
        ${this.labelTemplate}
      </div>
    `;
  }

  /**
   * HTML structure to be used as radio visible dot
   * @return {TemplateResult} HTML of radio dot
   */
  get inputTemplate() {
    return html`
      <slot name="input-template">
        <span class="radio">
          <span class="radio-check"></span>
        </span>
      </slot>
    `;
  }

  /**
   * Text to be used as label for the radio
   * @return {TemplateResult} HTML of label
   */
  get labelTemplate() {
    return html`
      <span class="label">
        <slot></slot>
      </span>
    `;
  }

  /**
   * Native radio to be moved to light DOM for communication with native forms
   * @return {TemplateResult} HTML of native radio
   */
  get _radio() {
    return html`
      <span class="slot">
        <slot name="_radio"></slot>
      </span>
      <input
        slot="_radio"
        id="${this._formElementId}"
        aria-hidden="true"
        tabindex="-1"
        type="radio"
        ?disabled="${this._disabled}"
        ?required="${this.required}"
        ?checked="${this.checked}"
        name="${ifDefined(this.name)}"
        @change="${this._onChange}"
        @input="${this._onInput}"
        .value="${ifDefined(this.value)}"
        form="${ifDefined(this.form)}">
    `;
  }

  _queryParentRoot(elem, query) {
    const rootNode = elem.getRootNode() || document.documentElement;
    return Array.from(rootNode.querySelectorAll(query));
  }

  _queryGroup(active = false) {
    if (this.name) {
      return this._queryParentRoot(this, `${this.nodeName.toLowerCase()}[name=${this.name}]${active ? `:not([aria-disabled=true])` : ''}`);
    }
    return [];
  }

  _uncheckGroup() {
    const group = this._queryGroup();
    for (const radio of group) {
      if (radio !== this) {
        radio.checked = false;
        radio._updateTabIndex();
      }
    }
  }

  _updateTabIndex() {
    this.setAttribute('tabindex', (this.checked || !this._isGroupChecked()) && !this._disabled ? '0' : '-1');
  }

  _isGroupChecked() {
    return this._queryGroup().find(radio => radio.checked);
  }

  _setInitialAttribute(attr, value) {
    if (!this.hasAttribute(attr)) {
      this.setAttribute(attr, value);
    }
  }

  _onInput(e) {
    e.preventDefault();
    e.stopPropagation();
  }

  _onChange(e) {
    e.preventDefault();
    e.stopPropagation();
    this.onClick(e);
  }

  /**
   * Sets checked to true if radio is not disabled
   * @param  {event} e Original click event
   */
  onClick(e) {
    if (this._disabled) {
      e.preventDefault();
      e.stopPropagation();
      e.stopImmediatePropagation();
      return;
    }
    this.checked = true;
  }

  /**
   * Checks radio on spacebar or Enter keyup if radio is currently being pressed
   * @param  {event} e Original keyup event
   */
  onKeyUp(e) {
    if ((e.keyCode === 13 || e.keyCode === 32) && this.active) {
      this.active = false;
      this.checked = true;
      e.preventDefault();
      e.stopPropagation();
    }
  }

  _dispatchEvent(event) {
    this.dispatchEvent(new CustomEvent(event, {
      bubbles: true
    }));
  }

  /**
   * Sets active state on element on spacebar or Enter keydown, and inits rowing if arrows are pressed
   * @param  {event} e Original keydown event
   */
  onKeyDown(e) {
    const code = e.keyCode;
    if (code === 13 || code === 32) {
      this.active = true;
      e.preventDefault();
      e.stopPropagation();
    }
    if (code >= 37 && code <= 40) {
      this._row(e);
    }
  }

  _row(e) {
    const group = this._queryGroup(true);
    const currentIndex = group.indexOf(this);
    const newIndex = this._nextIndex(group, currentIndex, e.keyCode);
    if (newIndex !== null) {
      const newCheckedRadio = group[newIndex];
      newCheckedRadio.checked = true;
      newCheckedRadio.focus();
      e.stopPropagation();
      e.preventDefault();
    }
  }

  _nextIndex(group, currentIndex, keyCode) {
    if (group.length === 0) {
      return null;
    }

    switch (keyCode) {
      case 39:
      case 40:
        return currentIndex + 1 > group.length - 1 ? 0 : currentIndex + 1;
      case 37:
      case 38:
        return currentIndex - 1 < 0 ? group.length - 1 : currentIndex - 1;
    }

    return null;
  }
}

customElements.define(CellsRadio.is, CellsRadio);
