import { css, unsafeCSS } from 'lit-element';
import * as foundations from '@bbva-web-components/bbva-foundations-styles/bbva-foundations-styles.js';

export default css`:host {
  --_color: var(--bbva-form-radio-button-color, var(--colorsSecondary600, ${unsafeCSS(foundations.colors.secondary600)}));
  --_error-icon-color: var(--bbva-form-radio-button-error-icon-color, var(--colorsTertiaryType1Dark, ${unsafeCSS(foundations.colors.tertiaryType1Dark)}));
  display: block;
  box-sizing: border-box;
  color: var(--_color);
  @apply --bbva-form-radio-group; }

:host([hidden]), [hidden] {
  display: none !important; }

::slotted(.form-radio-group-container) {
  display: contents; }

.error {
  margin-top: calc(((1 * var(--gridSpacerVariant, var(--gridSpacer, ${unsafeCSS(foundations.grid.spacer)}))) / 16) * 1rem);
  margin-left: calc(((2 * var(--gridSpacerVariant, var(--gridSpacer, ${unsafeCSS(foundations.grid.spacer)}))) / 16) * 1rem);
  display: flex;
  align-items: center;
  @apply --bbva-form-radio-group-error; }
  .error-icon {
    width: calc(((2 * var(--gridSpacerVariant, var(--gridSpacer, ${unsafeCSS(foundations.grid.spacer)}))) / 16) * 1rem);
    height: calc(((2 * var(--gridSpacerVariant, var(--gridSpacer, ${unsafeCSS(foundations.grid.spacer)}))) / 16) * 1rem);
    margin-right: calc(((1 * var(--gridSpacerVariant, var(--gridSpacer, ${unsafeCSS(foundations.grid.spacer)}))) / 16) * 1rem);
    color: var(--_error-icon-color);
    @apply --bbva-form-radio-group-error-icon; }
  .error-message {
    font-size: var(--typographyType3XSmall, ${unsafeCSS(foundations.typography.type3XSmall)});
    line-height: var(--lineHeightType3XSmall, ${unsafeCSS(foundations.lineHeight.type3XSmall)});
    @apply --bbva-form-radio-group-error-message; }
`;