export class BbvaFormRadioButton extends CellsRadio {
  static get is() {
    return 'bbva-form-radio-button';
  }

  static get properties() {
    return {
      /**
       * Icon for error message
       */
      errorIcon: {
        type: String,
        attribute: 'error-icon',
      },

      /**
       * Size for error icon
       */
      errorIconSize: {
        type: Number,
        attribute: 'error-icon-size',
      },

      /**
       * Error message
       */
      errorMessage: {
        type: String,
        attribute: 'error-message',
      },

      /**
       * Disabled state of radio button
       */
      disabled: {
        type: Boolean,
      },

      /**
       * Required state of radio button
       */
      required: {
        type: Boolean,
        reflect: true,
      },

      /**
       * Invalid state of radio button
       */
      invalid: {
        type: Boolean,
        reflect: true,
      },

      /**
       * Readonly state of radio button
       */
      readonly: {
        type: Boolean,
        reflect: true,
      },
    };
  }

  constructor() {
    super();
    this.errorIcon = 'coronita:alert';
    this.errorIconSize = 16;
  }

  updated(changedProps) {
    /* istanbul ignore else */
    if (super.updated) {
      super.updated(changedProps);
    }
    if (changedProps.has('required')) {
      this.setAttribute('aria-required', this.required.toString());
    }
    if (changedProps.has('invalid')) {
      this.setAttribute('aria-invalid', this.invalid.toString());
    }
  }

  static get shadyStyles() {
    return `
      ${bbvaFoundationsStylesFocus().cssText}
      ${styles.cssText}
      ${getComponentSharedStyles('bbva-form-radio-button-shared-styles').cssText}
    `;
  }

  /**
   * Text to be used as label for the radio
   * @return {TemplateResult} HTML of label
   */
  get labelTemplate() {
    return html`
      <span class="content">
        <span class="main">
          <span class="label">
            <slot></slot>
          </span>
          <span class="description">
            <slot name="description"></slot>
          </span>
          ${this.invalid && this.errorMessage
            ? html`
                <span class="error">
                  <cells-icon
                    class="error-icon"
                    .icon="${this.errorIcon}"
                    .size="${this.errorIconSize}"
                  ></cells-icon>
                  <span class="error-message">${this.errorMessage}</span>
                </span>
              `
            : ''}
        </span>
        <span class="clip">
          <slot name="clip"></slot>
        </span>
      </span>
    `;
  }
}

customElements.define(BbvaFormRadioButton.is, BbvaFormRadioButton);

