export class BbvaFormRadioGroup extends cellsSlotsContentMixin(LitElement) {
  static get is() {
    return 'bbva-form-radio-group';
  }

  static get properties() {
    return {
      /**
       * Name for the items for form participation
       */
      name: {
        type: String,
        reflect: true,
      },
      /**
       * Array of items to show
       */
      items: {
        type: Array,
      },
      /**
       * Index of currently active item
       */
      selected: {
        type: Number,
        reflect: true,
      },

      /**
       * Readonly state of radio group
       */
      readonly: {
        type: Boolean,
      },

      /**
       * Required state of radio group
       */
      required: {
        type: Boolean,
      },

      /**
       * Disabled state of radio group
       */
      disabled: {
        type: Boolean,
      },

      /**
       * Invalid state of radio group
       */
      invalid: {
        type: Boolean,
        reflect: true,
      },

      /**
       * Icon for error message
       */
      errorIcon: {
        type: String,
        attribute: 'error-icon',
      },

      /**
       * Size for error icon
       */
      errorIconSize: {
        type: Number,
        attribute: 'error-icon-size',
      },

      /**
       * Error message
       */
      errorMessage: {
        type: String,
        attribute: 'error-message',
      },
    };
  }

  constructor() {
    super();
    this.items = [];
    this.selected = -1;
    this.errorIcon = 'coronita:alert';
    this.errorIconSize = 16;
    this.name = `form-radio-group-${uniqueID()}`;
    this.addEventListener('slotschanges', () => {
      const radios = this.querySelectorAll('bbva-form-radio-button');
      /* eslint-disable no-param-reassign */
      radios.forEach(item => {
        item.name = this.name;
      });
      /* eslint-enable no-param-reassign */
    });
  }

  connectedCallback() {
    /* istanbul ignore else */
    if (super.connectedCallback) {
      super.connectedCallback();
    }

    if (!this.getAttribute('role')) {
      this.setAttribute('role', 'radiogroup');
    }
  }

  /**
   * Returns current items of group
   * @return {Object} Array of bbva button group items
   */
  get radioItems() {
    return Array.from(this.querySelectorAll('bbva-form-radio-button'));
  }

  firstUpdated(changedProps) {
    /* istanbul ignore else */
    if (super.firstUpdated) {
      super.firstUpdated(changedProps);
    }
    this._itemsContainer = this.shadowRoot.querySelector('.form-radio-group-container');
    if (this._itemsContainer) {
      this.appendChild(this._itemsContainer);
    }
    this.addEventListener('change', ev => this._updateItems(ev));
  }

  updated(changedProps) {
    /* istanbul ignore else */
    if (super.updated) {
      super.updated(changedProps);
    }
    if (changedProps.has('selected') && this.radioItems.length) {
      if (
        Number.isFinite(this.selected) &&
        this.selected > -1 &&
        this.selected < this.radioItems.length
      ) {
        this.radioItems[this.selected].checked = true;
      } else {
        const currentlyChecked = this.radioItems.find(item => item.checked);
        if (currentlyChecked) {
          currentlyChecked.checked = false;
        }
      }
    }
    if (changedProps.has('disabled') && this.radioItems.length) {
      this.setAttribute('aria-disabled', this.disabled.toString());
      this.radioItems.forEach((item, index) => {
        this.radioItems[index].disabled = this.disabled;
      });
    }
    if (changedProps.has('invalid') && this.radioItems.length) {
      this.setAttribute('aria-invalid', this.invalid.toString());
      this.radioItems.forEach((item, index) => {
        this.radioItems[index].invalid = this.invalid;
      });
    }
    if (changedProps.has('readonly') && this.radioItems.length) {
      this.setAttribute('aria-readonly', this.readonly.toString());
      this.radioItems.forEach((item, index) => {
        this.radioItems[index].readonly = this.readonly;
      });
    }
    if (changedProps.has('required') && this.radioItems.length) {
      this.setAttribute('aria-required', this.required.toString());
      this.radioItems.forEach((item, index) => {
        this.radioItems[index].required = false;
      });
      this.radioItems[0].required = true;
    }
  }

  _updateItems(ev) {
    const index = this.radioItems.indexOf(ev.target);
    this.selected = index;
    this.invalid = false;
    /**
     * Fired when selected item changes
     * @event bbva-form-radio-group-change
     * @param { String } value Value of selected item
     * @param { Number } selected Index of selected item
     */
    this.dispatchEvent(
      new CustomEvent('bbva-form-radio-group-change', {
        bubbles: true,
        composed: true,
        detail: {
          value: ev.target.value,
          selected: index,
        },
      }),
    );
  }

  static get shadyStyles() {
    return `
      ${groupStyles.cssText}
      ${getComponentSharedStyles('bbva-form-radio-group-shared-styles').cssText}
    `;
  }

  render() {
    return html`
      <style>
        ${this.constructor.shadyStyles}
      </style>
      <slot></slot>
      <div class="form-radio-group-container">
        ${this.items.map(
          item => html`
            <bbva-form-radio-button
              .name="${this.name}"
              .value="${ifDefined(item.value)}"
              divider="${ifDefined(item.divider)}"
              ?disabled="${item.disabled}"
              ?checked="${item.checked}"
              ?required="${item.required}"
              >${item.text}</bbva-form-radio-button
            >
          `,
        )}
      </div>
      ${this.invalid && this.errorMessage
        ? html`
            <span class="error">
              <cells-icon
                class="error-icon"
                .icon="${this.errorIcon}"
                .size="${this.errorIconSize}"
              ></cells-icon>
              <span class="error-message">${this.errorMessage}</span>
            </span>
          `
        : ''}
    `;
  }
}
customElements.define(BbvaFormRadioGroup.is, BbvaFormRadioGroup);
